
.clear {
	clear: both;
}
.list-brand {
	color: #000000;
	background-color: #ffffff;
}
.wrap {
	width: 90%;
	margin: 0 auto;
}
.list-brand .top {
	height: 55px;
	background-color: #FFFFFF;
	margin-top: 45px;
}
.list-brand .top .logo {
	height: 27px;
	width: 173px;
	background-image: url(http://www.adidas.com.cn/media/wysiwyg/adidas_IMG/2016/v2/top-5-athletics-top.png);
	float: left;
	margin-top: 19px;
}
.list-brand .top .menu {
	float: left;
	margin-top: 25px;
	margin-left: 15px;
}
.list-brand .top .menu ul li {
	display: inline-block;
}
.list-brand .top .menu ul li a {
	color: #000;
	display: block;
	margin-right: 15px;
	text-transform: uppercase;
	font-size: 2px;
}
.list-brand .top .keep {
	float: right;
	height: 25px;
	width: 25px;
	background-color: #000000;
}
.list-brand .brand-menu {
	height: 35px;
	background-color: #000000;
	border-bottom: 1px #eeeeee solid;
}
.list-brand .brand-menu ul li a {
	float: left;
	color: #FFFFFF;
	margin: 0px 22px;
	padding-top: 7px;
}
.list-brand .brand-banner {
	height: 245px;
	background-color: #ffffff;
	color: #000000;
}
.list-brand .brand-banner .banner-wrap {
	width: 70%;
	height: 100%;
	margin: 0 auto;
}
.list-brand .brand-banner .banner-wrap .leftPic {
	float: left;
	width: 72%;
	height: 100%;
}
.list-brand .brand-banner .banner-wrap .rightInfo {
	float: left;
	width: 28%;
	text-align: right;
	margin-top: 15%;
}
.list-brand .brand-banner .banner-wrap .rightInfo h1 {
	font-size: 22px;
}
.list-brand .brand-banner .banner-wrap .rightInfo h2 {
	font-size: 20px;
}
.list-brand .brand-banner .banner-wrap .rightInfo p {
	color: #999;
	font-size: 1px;
}
.list-brand .brand-banner .banner-wrap .rightInfo .btn {
	margin-top: 10px;
	border: 1px solid #C5C5C5;
}
.list-brand .brand-banner .banner-wrap .rightInfo .btn a {
	font-size: 2px;
	margin: 5px 10px;
}
.list-brand .main {
	background-color: #ffffff;
	overflow: hidden;
}
.list-brand .main .swiper-container {
	background-color: #ffffff;
	margin: 16px 0;
}
.list-brand .main .swiper_img {
	height: 7rem;
	width: 100%;
	display: block;
}
.list-brand .main h2 {
	padding-top: 15px;
	padding-bottom: 15px;
	line-height: 20px;
	color: #000000;
}
.list-brand .main .main-wrap {
	width: 90%;
	margin: 0 auto;
}
.list-brand .main .main-top {
	height: 55px;
}
.list-brand .main .main-top .leftInfo {
	padding-top: 15px;
	float: left;
	width: 40%;
}
.list-brand .main .main-top .leftInfo p {
	color: #999;
	font-size: 1px;
}
.list-brand .main .main-top .rightShare {
	float: left;
	width: 50%;
	height: 100%;
	margin-left: 25px;
}
.list-brand .main .main-top .rightShare .sharePic {
	float: left;
	width: 30%;
	height: 55px;
	padding: 7px 0;
}
.list-brand .main .main-top .rightShare .shareInfo {
	float: left;
	width: 70%;
	padding-top: 10px;
	border-bottom: 1px darkslategray solid;
}
.list-brand .main .main-top .rightShare .shareInfo p {
	float: left;
	padding-left: 45px;
	color: #000000;
	font-size: 16px;
}
.list-brand .main .goods-box {
	border-left: 1px #EEEEEE solid;
	border-bottom: 1px #EEEEEE solid;
	margin: 16px 0;
	overflow: hidden;
}
.list-brand .main .goods-box .goods {
	height: 240px;
	float: left;
	width: 50%;
	height: 100%;
	border-right: 1px #EEEEEE solid;
	border-top: 1px #EEEEEE solid;
}
.list-brand .main .goods-box .goods .wrap {
	margin: 10px 10px;
}
.list-brand .main .goods-box .goods .goodsTitle {
	color: #555;
	font-size: 0.9em;
	text-transform: uppercase;
	font-weight: 500;
	text-align: left;
	text-indent: 8px;
}
.list-brand .main .goods-box .goods p {
	text-align: left;
	text-indent: 8px;
	color: #999;
	font-size: 1px;
}
.list-brand .main .goods-box .goods h4 {
	text-align: left;
	text-indent: 8px;
	color: #000000;
	font-weight: 500;
	line-height: 35px;
}
.list-brand .main .goods-box .goods img {
	padding-top: 10px;
}
.list-brand .main .goods-box .goods .addCar {
	background-color: #CBCBCB;
	height: 30px;
	border-radius: 8px;
	width: 150px;
	margin-left: 8px;
}
.list-brand .main .goods-box .goods .addCar .addCarPic {
	width: 19%;
	height: 30px;
	background-image: url(../../../../../web/common/img/loading.png);
	float: left;
	margin-left: 10px;
}
.list-brand .main .goods-box .goods .addCar .addCarTxt {
	margin-left: 40px;
	width: 70%;
	height: 30px;
	margin-top: -27px;
	float: left;
	color: #ffffff;
	line-height: 33px;
	font-size: 14px;
}
.list-brand .main .sale {
	margin: 16px 0;
}
.list-brand .main .sale .sale-goods {
	width: 33.3%;
	-webkit-box-flex: 1;
	    -ms-flex-positive: 1;
	        flex-grow: 1;
	position: relative;
	padding: 1%;
	float: left;
}
.list-brand .main .sale .sale-goods img {
	width: 100%;
	height: 100%;
}
.list-brand .main .sale .sale-goods p {
	position: absolute;
	left: 0;
	bottom: 0;
	background-color: rgba(255, 255, 255, .8);
	width: 100%;
	font-size: .4rem;
	color: black;
	padding: .1rem;
}
.list-brand .main .sale .sale-goods p span {
	display: block;
	font-size: .8rem;
	color: black;
	font-family: "";
}
.list-brand .bottom {
	height: 45px;
	background-color: #000000;
}
.list-brand .bottom .picTxt {
	float: left;
	height: 45px;
	width: 50%;
	background-color: #000000;
}
.list-brand .bottom .picTxt img {
	float: left;
	margin-top: 12px;
	width: 20px;
	height: 20px;
}
.list-brand .bottom .picTxt p {
	margin-top: 14px;
	color: #ffffff;
	font-size: 1px;
}
.list-brand .men-menu {
	height: 19px;
}
.list-brand .men-menu ul li {
	/*float: left;
  color: #000000;
  font-size: 13px;
  margin:0px 28px;
  padding-top: 7px;*/
}
.list-txt {
	font-size: 12px;
	background-color: #000000;
	color: #ffffff;
}
.list-brand .brand-list {
	width: 100%;
	background-color: #ffffff;
}
.list-brand .brand-list .brand-info {
	width: 100%;
	padding: 10px 0;
	background-color: #ffffff;
	border-bottom: 1px #EEEEEE solid;
	overflow: hidden;
	height: 150px;
}
.list-brand .brand-list .brand-info .info-wrap {
	height: 4.72rem;
}
.list-brand .brand-list .brand-info .brand-pic {
	float: left;
}
.list-brand .brand-list .brand-info .brand-pic img {
	width: 130px;
	height: 120px;
}
.list-brand .brand-list .brand-info .brand-txt {
	margin-left: 155px;
	height: 6.64rem;
}
.list-brand .brand-list .brand-info .brand-txt .brandTitle {
	font-size: .64rem;
	display: -webkit-box;
	height: 1.76rem;
	line-height: .9rem;
	margin-top: 0rem;
	text-align: left;
}
.list-brand .brand-list .brand-info .brand-txt h4 {
	font-size: 1px;
	text-align: left;
	color: #262626;
	font-weight: 100;
	float: left;
}
.list-brand .brand-list .brand-info .brand-txt h5 {
	margin-top: 1.7rem;
	margin-left: -.16rem;
	text-align: left;
	font-size: .96rem;
	font-weight: normal;
}
.list-brand .brand-list .brand-info .brand-txt span {
	color: #ffffff;
	position: absolute;
	margin-top: -50px;
	margin-left: 40px;
	font-size: 15px;
	width: 45px;
	height: 45px;
	font-size: 25px;
	line-height: 45px;
	border-radius: 1rem;
	font-family: "";
	background-color: #fd4433;
}
.list-brand .brand-list .brand-info .brand-txt span a {
	color: #ffffff;
}
.list-brand .activeTitle {
	background-color: #000000;
	color: #ffffff;
}
.list-brand .titleItem {
	float: left;
	font-size: 13px;
	padding-left: 47.7px;
	padding-right: 20px;
	text-align: center;
	border-bottom: 1px #eeeeee solid;
}
.list-brand .L {
	background-color: #000000;
	color: #ffffff;
}
.list-brand #fullbg {
	height: 785px;
	width: 500px;
	display: block;
	background-color: gray;
	left: 0;
	opacity: 0.5;
	position: absolute;
	top: 0;
	z-index: 3;
	filter: alpha(opacity=50);
	-moz-opacity: 0.5;
	-khtml-opacity: 0.5;
}
.list-brand .shadown {
	display: none;
}
.list-brand #dialog {
	background-color: #fff;
	border: 5px solid rgba(0, 0, 0, 0.4);
	height: 230px;
	left: 45%;
	margin: -100px -100px;
	padding: 1px;
	position: fixed !important;
	/*  */
	position: absolute;
	top: 50%;
	width: 250px;
	z-index: 5;
	border-radius: 5px;
	display: block;
}
.list-brand #dialog p {
	margin: 0 0 12px;
	height: 30px;
	line-height: 30px;
	background: #FD4433;
}
.list-brand #dialog p.close {
	text-align: right;
	padding-right: 10px;
}
.list-brand #dialog p.close a {
	color: #fff;
	text-decoration: none;
	border: 1px #ffffff solid;
	font-size: 13px;
}
.list-brand #dialog p.close em {
	color: #fff;
	font-size: 13px;
	margin-right: 152px;
}
.list-brand #dialog .selectColor {
	color: #cccccc;
	margin-right: 10px;
}
.list-brand #dialog h5 {
	color: #000000;
	margin-right: 50px;
	position: absolute;
	left: 50px;
	bottom: 40px;
}
.list-brand #dialog .dialog-wrap {
	margin: 0 10px;
	height: 80%;
	width: 90%;
	text-align: left;
}
.list-brand #dialog .dialog-wrap p {
	background-color: #ffffff;
	margin-top: 10px;
}
.list-brand #dialog .dialog-wrap .selectIt {
	height: 20px;
	margin: 10px 0;
}
.list-brand #dialog .show-info {
	height: 20px;
	margin-top: 50px;
}
.list-brand .activeColor {
	border: 1px #CBCBCB solid;
	color: #777777;
}
.list-brand .addSure {
	margin-top: 15px;
	border: 1px #CBCBCB solid;
	color: #FFFFFF;
	background-color: #fd4433;
	width: 100px;
	border-radius: 5px;
	text-align: center;
	margin-left: 50px;
}



