
@import "../../reset.css";
@import '../../static/swiper.min.css';
.home {
	background-color: #ffffff;
	overflow: hidden
}
.home .vux-slider>.vux-swiper>.vux-swiper-item>a>.vux-swiper-desc {

	font-size: 14px;
}
.home .banner-index {
	background-color: #ffffff;
}
.home .swiper_img {
	height: 7rem;
	width: 100%;
	display: block;
}
.home .banner-index {
	position: relative;
}
.index-tit1 :after {
	position: absolute;
	right: .44rem;
	top: 50%;
	/* color: #999; */
	display: inline-block;
	border: solid #999;
	border-width: .06rem 0 0 .06rem;
	-webkit-transform: rotate(135deg);
	margin-top: -0.15rem;
	width: .24rem;
	height: .24rem;
	content: '';
}
.home .banner-name {
	position: absolute;
	left: 0;
	bottom: 0;
	display: block;
	width: 100%;
	height: 1.4rem;
	line-height: 1.4rem;
	background: rgba(0, 0, 0, .5);
	color: #fff;
	text-align: left;
	padding-left: .4rem;
	font-size: .6rem;
}



























/**/
.home .index-active {
	width: 100%;
	min-height: 4.8rem;
	padding-top: 0.66rem;
	background: #fff;
}
.home .index-active ul li {
	width: 100%;
	text-align: center;
	width: 25%;
	float: left;
}
.home .index-active ul li img {
	width: 1.96rem;
	height: 1.96rem;
	margin: 0 auto;
}
.home .index-active ul li .active-info {
	height: 1.52rem;
	padding-top: 0.4rem;
	font-size: 0.48rem;
	color: #666;
}
.home .News {
	border-top: 1px solid #F3F3F3;
	border-bottom: 1px solid #F3F3F3;
	overflow: hidden;
	height: 2rem;
}
.home .News img {
	width: 20%;
	float: left;
	margin-left: 2%;
	margin-top: .5rem;
}
.home .News .news {
	width: 75%;
	height: 2rem;
	line-height: 2rem;
	float: left;
	text-align: left;
}
.home .News .news p {
	font-size: .5rem;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	text-align: center;
}
.home .News .news p em {
	color: #f00;
	margin: 0 5px;
	font-size: .6rem;
}
.home .vux-slider>.vux-swiper>.vux-swiper-item>a>.vux-swiper-desc {

	padding: 20px 55px 29px 20px;

	text-align: left;
}
.index-design {
	width: 100%;
	height: auto;
	position: relative;
}
.index-tit1 h3 {
	color: #232323;
	text-align: left;
}
.index-tit1 h3 span {
	float: right;
	padding-right: .3rem;
	font-size: .48rem;
	color: #666;
	font-weight: 100;
}
.index-design .design-banner img {
	width: 14.12rem;
	height: 7.52rem;
}
.index-design .design-banner .design-box {
	width: 12.6rem;
	height: 2.7rem;
	position: absolute;
	top: 6.48rem;
	left: 50%;
	margin-left: -6.3rem;
	background-size: 11.6rem auto;
	background-position: .52rem bottom;
}
h3 {
	font-size: 100%;
	font-weight: 500;
}
body,
div,
h3,
img,
li,
p,
ul {
	margin: 0;
	padding: 0;
	font-family: Arial, PingFangSC-Regular, Helvetica, "Droid Sans", sans-serif;
}
.clearfix:after {
	visibility: hidden;
	display: block;
	font-size: 0;
	content: ".";
	clear: both;
	height: 0;
	overflow: hidden;
}
.index-design .design-banner .design-box .design-tip {
	width: 100%;
	height: 2.4rem;
	border: 1px solid #eee;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	text-align: center;
	background: #fff;
	box-shadow: 3px 3px 4px #eee;
}
.design-box .design-tip .des {
	width: 8.92rem;
	font-size: .56rem;
	margin-left: 1.84rem;
	margin-right: 1.84rem;
	color: #232323;
}
.index-design .design-banner {
	width: 14.12rem;
	height: 9.4rem;
	margin: 0 .94rem;
	position: relative;
}
.index-tit1 {
	width: 100%;
	height: 1.8rem;
}
.index-tit1 {
	display: block;
	width: 100%;
	height: 1.8rem;
	padding: .44rem;
	position: relative;
	line-height: .92rem;
	font-size: .56rem;
}
.index-design .index-tit {
	padding: .44rem;
	font-size: .56rem;
	background: #fff;
}
.new-list {
	background: #fff;
	padding-left: 17px;
}
.new-list li {
	float: left;
	width: 50%;
	height: auto;
	margin-bottom: 0.4rem;
	margin-right: 15px;
}
.new-list .goodsItem-img img {
	width: 5.2rem;
	height: 5.2rem;
}
.new-list li {
	display: block;
	width: 6.92rem;
	border: 1px solid #eee;
}
.new-list .info-bar .tit {
	display: -webkit-box;
	font-size: 0.52rem;
	height: 1.16rem;
	line-height: 0.64rem;
	width: 100%;
	text-align: center;
	overflow: hidden;
	text-overflow: ellipsis;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
}
.new-list .info-bar {
	height: 2.96rem;
	padding: 0.2rem 0.6rem;
}
.new-list .info-bar .price {
	font-size: 0.76rem;
	margin-top: 0.4rem;
	margin-left: -0.06rem;
	text-align: center;
	color: #eb6100;
	width: 100px;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}



