
.activityInfo h1 {
		width: 100%;
		font-weight: 500;
		color: #000000;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		font-size: 18px;
		margin-bottom: 10px;
}
.activityInfo h2 {
		width: 100%;
		font-weight: 100;
		color: #ADADAD;
		font-size: 15px;
		margin-bottom: 10px;
}
.activityInfo h4 {
		width: 100%;
		font-weight: 100;
		color: red;
		font-size: 17px;
		margin-bottom: 10px;
}
.activityInfo .cont {
		background-color: #ffffff;
}
.activityInfo .task {
		margin: .17rem 0;
		overflow: hidden;
		border-bottom: .04rem #EEEEEE solid;
		border-bottom-right-radius: .21rem;
		border-bottom-left-radius: .21rem;
}
.activityInfo label {
		margin-right: .4rem;
		width: 60px;
		font-size: .6rem;
		color: #666;
		float: left;
		text-align: left;
}
.activityInfo label i {
		font-style: normal;
		font-size: .6rem;
		line-height: 26px;
}
.activityInfo .cont input {
		outline: none;
		color: #333;
		height: 35px;
		border: none;
		float: left;
		font-size: .6rem;
		margin-top: -8px;
}
.activityInfo em{
		margin-right: 10px;
		font-style: normal;
    	font-size: .6rem;
    	line-height: 10px;
}



