
.logMain{
	background-color: #EB6100;
	min-height:100vh;
	color: #ffffff;
}
.login {
	width: 100%;
	color: #fff;
}
.logMain .close-pic {
	width: .8rem;
	margin-left: .6rem;
	display: block;
}
.logMain .logo-pic {
	display: block;
	width: 6.41rem;
	height:6.41rem;
	margin-left: auto;
	margin-right: auto;
	margin-top: 1rem;
	margin-bottom: 1rem;
}
.login p,.register p {
	width: 80%;
	margin-left: auto;
	margin-right: auto;
	line-height: 2rem;
	overflow: hidden;
	font-size: .7rem;
}
.login input,.register input {
	width: 100%;
	vertical-align: middle;
	height: 2rem;
	padding-left: .21rem;
	border-radius: 5rem;
	border: none;
	margin-bottom: .5rem;
	text-indent: 1em;
	color: #999;
	font-size: .56rem;
}
.register p.verif{
	position: relative;
}
.register .verif-btn{
	width: 30%;
	display: inline-block;
	position: absolute;
	right: 0;
	top: 0;
	background-color: #e8a274;
	color: #fff;
	border-top-right-radius: 2rem;
	border-bottom-right-radius: 2rem;
	/*border-bottom: 1px solid #7CCAF1;*/
	font-size: .5rem;
}
.register .txt-info{
    color: #fff;
    line-height: 1rem;
    text-align: left;
    font-size: .5rem;
    padding-left: .42rem;
}
.login input::-webkit-input-placeholder,.register input::-webkit-input-placeholder {
	color: #999;
	opacity: 1;
}
.login a,.register a {
	color: #fff;
	font-size: .7rem;
}
.login .login-btn,.register .register-btn {
	display: inline-block;
	padding: 2px 1.1rem;
	border-radius: 5rem;
	width: 100%;
	background-color: #e8a274;
	border: none;
	height: 2rem;
	line-height: 2rem;
	letter-spacing: .21rem;
	margin-top: 1rem;
	font-size: .76rem;
}



