
.order .box2-wrap {
	height: 300px;
	overflow: hidden;
}
.order .pulldown-arrow {
	display: inline-block;
	transition: all linear 0.2s;
	color: #666;
	font-size: 25px;
}
.order .ps-detail-content {
	z-index: 1;
	margin-top: 46px;
}
.order .com-list {
	margin-bottom: .5rem;
}
.order .com-list>li {
	font-size: .6rem;
	border-bottom: 1px solid #eee;
	background: #ffffff;
}
.order .label {
	font-size: .44rem;
	padding: 0 .16rem;
	line-height: .16rem!important;
	color: #fff;
	margin-left: .5rem;
	vertical-align: middle;
	background: #eb6100;
	height: .16rem;
}
.order .wbox {
	display: -webkit-flex!important;
	display: -webkit-box;
}
.order .wbox-flex {
	-webkit-flex: 1!important;
	/* -webkit-box-flex: 1; */
}
.order .order-item {
	padding: 0.4rem 0.52rem;
}
.order .order1 .pro-img img {
	width: 3.08rem;
	height: 3.08rem;
	border-radius: 0;
	margin-right: 0.6rem;
	border: 0
}
.order .order1 .price {
	line-height: .8rem;
	color: #000;
	font-size: .6rem;
	margin-top: 0.2rem;
}
.order .order1 .color-line {
	padding: .2rem 0 .12rem
}
.order .order1 .color-line .style {
	display: -webkit-box;
	-webkit-line-clamp: 1;
	height: .58rem
}
.order .order1 .num {
	font-size: .52rem;
	line-height: .8rem;
	margin-top: .2rem;
	text-align: right
}
.order .order1 .ordfd {
	border-top: .04rem solid #eee;
	padding: .12rem .52rem;
}
.order .order1 .ordfd .wbox-flex {
	line-height: 1.2rem
}
.order .com-list .button {
	line-height: 1.2rem;
	height: 1.2rem;
	padding: 0;
	width: 3.2rem;
	text-align: center;
	border-radius: 0;
	font-size: .6rem
}
.order .button.gray {
	background: #eee;
	color: #999999;
}
.order .vux-header .vux-header-left a,
.vux-header .vux-header-left button,
.vux-header .vux-header-right a,
.vux-header .vux-header-right button {

	color: white;
}
.order .vux-header .vux-header-left .left-arrow:before {

	border: 1px solid white;
	content: "";
	position: absolute;
	width: 12px;
	height: 12px;
	border-width: 1px 0 0 1px;
	-webkit-transform: rotate(315deg);
	transform: rotate(315deg);
	top: 8px;
	left: 7px;
}
.order .button,
.order button {
	color: #fff;
	background: #eb6100;
	font-size: .6rem;
	display: inline-block;
	padding: .14rem .3rem;
	border: none;
	cursor: pointer;
}
.order .shopTitle {
	width: 100px;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}
.order .shopTitle em {
	display: inline-block;
	width: 18px;
	height: 18px;
	background: url(../../imgs/shop.png);
	background-repeat: no-repeat;
	background-size: 100%;
	float: left;
	margin-right: 10px;
}




