
@import "../../reset.css";
#index_section {
	text-align: left;
}
#index_section .left {
	position: absolute;
	left: 0.5rem;
	top: 0.5rem;
	z-index:10;
}
#index_section .left .txt {
	color: #fff;
	font-size: 14px;
}
#index_section header {
	z-index: 5;
}
#index_section .login-img-box {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	background: url(../imgs/bg.jpg) no-repeat;
	background-size: 100% auto;
	z-index: 3;
}
#index_section article.lazyimg {
	position: absolute;
	bottom: 0;
	width: 100%;
	visibility: visible;
	display: block;
	background: 0 0;
	top: 0!important;
	z-index: 5;
	overflow: hidden;
}
#index_section article.lazyimg div a.n_logo {
	display: block;
	width: 10.96rem;
	height: 3.52rem;
	margin: 3.96rem auto 0;
	background: url(../imgs/logo1.png) center no-repeat;
	background-size: 8.36rem auto;
}
#index_section .btn-tips {
	width: 10.24rem;
	margin: 0 auto;
}
#index_section .btn-tips a {
	padding: .48rem 0;
	color: #fff;
	font-size: .52rem;
}
#index_section .n-agree {
	left: 0;
	right: 0;
	bottom: .64rem;
	position: absolute;
	z-index: 4;
	text-align: center;
}
#index_section .n-agree a {
	font-size: .48rem;
	color: #fff;
}



