
.coupon .ps-detail-content {
  z-index: 1;
  margin-top: 0;
  margin-bottom: 2rem;
}
.coupon .gift-list {
  padding: .4rem .52rem .1rem;
}
.coupon .gift-list li {
  width: 100%;
  height: 6.5rem;
  background: url(../../imgs/user_coupon.png) no-repeat;
  background-size: 100% auto;
  text-align: center;
}
.coupon .gift-list li .ticket-name {
  padding-top: .84rem;
  font-size: .48rem;
  color: #999;
}
.coupon .gift-list li .ticket-price {
  color: #eb6100;
  margin-top: .4rem;
  height: 1.7rem;
  line-height: 1;
  font-size: 2rem;
}
.coupon .gift-list li .ticket-info {
  height: .56rem;
  line-height: 1;
  margin-top: .44rem;
  font-size: .6rem;
  color: #eb6100;
}
.coupon .vux-header .vux-header-left a,
.vux-header .vux-header-left button,
.vux-header .vux-header-right a,
.vux-header .vux-header-right button {
  color: white;
}
.coupon .vux-header .vux-header-left .left-arrow:before {
  border: 1px solid white;
  content: "";
  position: absolute;
  width: 12px;
  height: 12px;
  border-width: 1px 0 0 1px;
  -webkit-transform: rotate(315deg);
  transform: rotate(315deg);
  top: 8px;
  left: 7px;
}
.coupon .gift-list li .ticket-timeLimit {
  font-size: .4rem;
  color: #999;
  margin-top: .28rem;
}



