
.orderInfo{
	background-color:#EEEEEE;
}
.orderInfo .delete{
	position: fixed;
	z-index: 99;
	background-color: #FFFFFF;
    width: 100%;
    padding: 10px 10px;
    border-top: 1px #EEEEEE solid;
    height: 50px; 
    bottom: 0px;
}
.orderInfo .delete .txt{
	line-height: 30px;  
	width: 90px;
	height: 30px;
	float: right;
	background-color:#EB6100 ;
	color:#FFFFFF ;
}
.orderInfo .delete .price{
	float: left;
}
.orderInfo .delete em{
	color: red;
}
.orderInfo .o-info{
	height: 110px;
	margin-top: 50px;
	overflow: hidden;
	padding: 10px;
	background-color: #FFFFFF;
}
.orderInfo .o-info h5{
	text-align: left;
	font-weight:normal;
	font-size: 15px;
	margin-bottom: 12px;
	color: #000000;
}
.orderInfo .o-info h5 em{
	font-size: .44rem;
	padding: 0 .16rem;
	line-height: .16rem!important;
	color: #fff;
	margin-left: .5rem;
	vertical-align: middle;
	background: #eb6100;
	height: .16rem;
	width: 80px;
}
.orderInfo .state{

	margin-top: 45px;
	border-bottom: 1px #d9d9d9 solid;
	height: 35px;
	line-height: 35px;
}
.orderInfo .address{
	height: 100px;
	padding-top: 15px;
	padding-right: 10px;
	margin-top: 5px;
	color: #000000;
	font-size: 15px;
	background-color: #FFFFFF;
}
.orderInfo .address h4{
	font-weight: normal;
	margin-bottom: 12px;
}
.orderInfo .address span{
	width: 80px;
	float: left;
}
.orderInfo .address .info{
	color: #999999;
	overflow: hidden;
}
.orderInfo .shopTitle{
	padding: 5px 15px 0px 15px;
    height: 27px;
    background-color: #FFFFFF;
}
.orderInfo h2{
	text-align: left;
	display: inline-block;
	font-size: 16px;
	float: left;
	margin-top: -2px;
	font-weight: normal;
}
.orderInfo .shopTitle b {
	display: inline-block;
	width: 18px;
	height: 18px;
	background: url(../../imgs/shop.png);
	background-repeat: no-repeat;
	background-size: 100%;
	float: left;
	margin-right: 10px;
}
.orderInfo .car_list .carItem{
		font-size: 15px;
		padding: 10px 10px;
		overflow: hidden;
		border-bottom: 1px #EEEEEE solid;
		background-color: #ffffff;
}
.orderInfo .carItem .pro-check{
		float: left;
		position: absolute;
		margin-top: 30px;
		width: 8%;
	
		
		height: 100%;
}
.orderInfo .carItem a{
	
		width: 90%;
		float: left;
}
.orderInfo .carItem img{
		width: 3.6rem;
    	height: 3.6rem;
    	float: left;
    	margin-right: .6rem;
    	margin-left: 5px;
}
.orderInfo .carItem .goodsInfo{
		float: left;
		text-align: left;
		width: 8.9rem;
}
.orderInfo .goodsInfo h3{
		font-weight: normal;
		font-size: 15px;
}
.orderInfo .goodsInfo h4{
		font-weight: normal;
		font-size:14px;	
		color: #999;
}
.orderInfo .goodsInfo .goodsInfoBottom{
		font-size: 15px;
		color: #eb6100;
		margin-top: 15px;
}
.orderInfo  .goodsInfoBottom b{
		font-weight: normal;
		position: absolute;
    	left: 340px;
    	color: #000000;
    	font-size: 15px;
}
.orderInfo .weui-cell:before{
		left: 0px;
}
.orderInfo .vux-label{
		text-align: left;
		font-size: 15px;
}
.orderInfo .vux-divider {
    display: table;
    white-space: nowrap;
    height: auto;
    overflow: hidden;
    line-height: 1;
    text-align: center;
    padding:0 0;
    color: #EB6100;
}
.orderInfo .weui-cell__ft{
	color: #000000;
	font-size: 15px;
}
.orderInfo .weui-cell:before {
    left: 4%;
    width: 93%;
}




