
@import "../../reset.css";
.fl{
		float: left;
}
.rl{
		float: right;
}
.accounts .null-car {
		height: 15rem;
		padding-top: 5.5rem;
		text-align: center;
		background-color: #EEEEEE;
}
.accounts .null-car .ico {
		background: url(../imgs/null1.png) no-repeat;
		width: 4.74rem;
		height: 4.74rem;
		background-size: 4.74rem auto;
		display: block;
		margin: 0 auto .68rem;
}
.accounts .null-car p {
		color: #999;
		font-size: .6rem;
}
.accounts .null-car .button {
		font-size: .6rem;
		display: inline-block;
		margin-top: 1.04rem;
		border: 1px solid #eb6100;
		color: #fff;
		background: 0 0;
		background-color: #eb6100;
		padding: 0.2rem 0.9rem;
}
.accounts .car_list{
}
.accounts .car_list .carItem{
	
		
		padding: 15px 10px;
		overflow: hidden;
		
		background-color: #ffffff;
}
.accounts .carItem .pro-check{
		float: left;
		position: absolute;
		margin-top: 30px;
		width: 8%;
	
		
		height: 100%;
}
.accounts .carItem a{
	
		width: 90%;
		float: left;
}
.accounts .carItem img{
		width: 3.6rem;
    	height: 3.6rem;
    	float: left;
    	margin-right: .6rem;
    	margin-left: 5px;
}
.accounts .carItem .goodsInfo{
		float: left;
		text-align: left;
		width: 8.9rem;
}
.accounts .goodsInfo h3{
		font-weight: normal;
		font-size: .66rem;
}
.accounts .goodsInfo h4{
		font-weight: normal;
		font-size: .36rem;	
		color: #999;
}
.accounts .goodsInfo .goodsInfoBottom{
		position: absolute;
		bottom: 15px;
		font-size: .66rem;
		color: #eb6100;
}
.accounts  .goodsInfoBottom b{
		font-weight: normal;
		position: absolute;
	    left: 230px;
	    bottom: 0px;
		color: #000000;
}
.foorer-nav1{
		background-color: #EB6100;
		bottom: 0;
		position: fixed;
		z-index: 99;
		width: 100%;
		height: 45px;
}
.foorer-nav1  .bot-price {
    width: 8rem;
    font-size: .48rem;
    height: 45px;
    overflow: hidden;
    background-color: #FFFFFF;
    position: relative;
    text-align: center;
    display: inline-block;
    float: left;
    border-top: 1px #eeeeee solid;
}
.foorer-nav1.car .bot-price em{
 	color: #ffffff;
}
.Jcheckall {
   position: absolute;
    left: .8rem;
    top: 50%;
    margin-top: -0.15rem;
    width: 80px;

    text-align: left;
}
.foorer-nav1 .bot-price .JAllPrice {
    color: #eb6100;
    font-size: .72rem;
	
    line-height: 45px;
    padding-left: 15px;
    text-align: left;
}
.foorer-nav1 .bot-price .JAllPrice em{
	color: #000000;
}
.foorer-nav1 .bot-price .JAllPrice+div {
    line-height: .7rem;
    padding-right: .36rem;
    text-align: right;
}
.accounts .header{
	height: 2rem;
	background-color: #ffffff;
	position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 99;
    line-height: 2rem;
    font-size: 18px;
    border-bottom: 1px #EEEEEE solid;
    padding-right: 20px;
    text-align: center;
}
.accounts .header span{
	margin-left: 20px;
}
.accounts .header em{
	height: 2rem;
	
    line-height: 2rem;
    font-size: 15px;
    float: right;
}
.accounts .car-content{
	margin-top: 2rem;
	margin-bottom: 2.2rem;
}
.accounts .carItem .goodsEdit{

		float: left;
		text-align: left;
		width: 8.9rem;
}
.accounts .goodsEdit{
	display: none;
}
.accounts .goodsEdit .count{
	border: 1px #EEEEEE solid;
	height: 1.6rem;
}
.accounts .goodsEdit .count-num {
    width: 5.44rem;
    height: 1.2rem;
    text-align: center;
    font-size: .6rem;
    color: #232323;
    border: none;
    margin: 0;
}
.accounts .count .count-min,.count-add{

    font-size: 1rem;
    color: #232323;
    text-align: center;
    width: 30px;
    display: inline-block;
}
.accounts .shopTitle{
	padding: 5px 15px;
}
.accounts h2{
	text-align: left;
    display: inline-block;
    font-size: 16px;
    float: left;
    margin-top: -2px;
    font-weight: normal;
}
.accounts .shopTitle b {
	display: inline-block;
	width: 18px;
	height: 18px;
	background: url(../imgs/shop.png);
	background-repeat: no-repeat;
	background-size: 100%;
	float: left;
	margin-right: 10px;
}
.accounts .info{
}
.accounts .info .weui-cell:before {
	left: 0px;
}
.accounts .info .vux-label{
	text-align: left;
}
.accounts .info .weui-label{
	text-align: left;
}
.accounts .info .accounts input{
	width: 100%;
}
.accounts .info .weui-cell__ft{
	color: #000000;
}
.accounts .info .vux-cell-box > div {
    padding-right: 0px;
    margin-left: 130px;
}
.accounts .infoItem{
	font-size: 17px;
	overflow: hidden;
}
.accounts .infoItem h5{
	float: left;
	font-weight:normal;
	font-size: 17px;
	display: inline-block;
	margin-right: 10px;
}
.accounts .infoItem em{
	float: left;
	font-weight: 400;
	font-size: 17px;
}
.accounts .infoItem b{
	float: left;
	font-weight: 400;
	font-size: 17px;
	color: #eb6100;
}
.accounts .infoItem h3{
	float: left;
	font-weight: 400;
	font-size: 15px;
}
.accounts .infoItem h4{
	float: right;
	font-weight: 400;
	font-size: 15px;
}
.accounts .infoItem span{
	float: left;
	font-weight: 400;
	font-size: 15px;
}
.accounts input{
	float: right;
    border: none;
    height: 20px;
    width: 100%;
}
.accounts .shopItem{
	margin-bottom: 20px;
}
.accounts .vux-divider{
	color: #EB6100;
}
.accounts .address{
	height: 100px;
}
.accounts .address .vux-popup-picker-value{
	float: left;
	display: none;
}
.accounts .address .scroller-content{
	position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding: 0px 5px;
}
.accounts .address .vux-cell-box:before{
	border: none;
}
.accounts .address .weui-cell{
	padding: 0px 0px;
	padding-right: 10px;
}
.accounts .address .weui-cell_access .weui-cell__ft:after{
	margin-top: 9px;
    margin-right: 5px;
}
.accounts .address em{
	display: inline-block;
    width: 38px;
    height: 45px;
	background: url(../imgs/address.png);
	background-repeat: no-repeat;
    background-size: 100%;
    float: left;
    margin-top: 30px;
}
.accounts .address .info{
	width: 78%;
	position: absolute;
    top: 9px;
    left: 48px;
	overflow: hidden;
}
.accounts .address h4{
	font-weight: 400;
	font-size: .66rem;
	margin-top: 3px;
}
.accounts .address .txt{
	margin-top: 26px;
	float: left;
	text-align: left;
	font-size: 14px;
}
.accounts .address i{
	display: inline-block;
	width: 6%;
	height: 22px;
	background: url(../imgs/shop.png);
	background-repeat: no-repeat;
	background-size: 100%;
	float: right;
	margin-top: -50px;
}
.accounts .newAdd span{
line-height: 50px;
	border-radius: 0;
	padding: 0!important;
	height: 50px;
	text-align: center;
	position: relative;
	-webkit-box-flex: 1;
	box-flex: 1;
	display: block;
	background: #eb6100;
	font-size: .64rem;
	color: white;
}



