
.editAddress {
	background-color: #ffffff;
	min-height: 100vh;
}
.editAddress .pageTitle {
	height: 2.15rem;
	line-height: 1.7rem;
	text-align: left;
	text-indent: 1rem;
	border-bottom: .1rem #db0000 solid;
	background-color: #ffffff;
}
.editAddress .vux-header .vux-header-left a {
	color: white;
}
.editAddress .vux-header .vux-header-left .left-arrow:before {
	content: "";
	position: absolute;
	width: 12px;
	height: 12px;
	border: 1px solid white;
	border-width: 1px 0 0 1px;
	-webkit-transform: rotate(315deg);
	transform: rotate(315deg);
	top: 8px;
	left: 7px;
}
.editAddress .pageTitle b {
	font-weight: 500;
	font-size: .6rem;
	line-height: 2rem;
	color: #db0000;
	float: left;
	text-indent: -.65rem;
}
.editAddress .pageTitle .addFinish {
	float: right;
	height: 2.1rem;
	width: 2.8rem;
	color: #FFFFFF;
	text-indent: .68rem;
	background-color: #EB6100;
	line-height: 2rem;
	font-size: .7rem;
}
.editAddress .cont {
	margin: 2.2rem .6rem;
	background-color: #ffffff;
}
.editAddress .task {
	line-height: 1.28rem;
	margin: .17rem 0;
	overflow: hidden;
	border-bottom: .04rem #EEEEEE solid;
	border-bottom-right-radius: .21rem;
	border-bottom-left-radius: .21rem;
}
.editAddress .address {
	margin: -.65rem 0;
	display: inline-block;
	float: left;
	width: 80%;
	margin-top: -1.86rem;
	margin-left: 4.28rem;
}
.editAddress label {
	margin-right: .4rem;
	width: 4rem;
	font-size: .6rem;
	color: #666;
	float: left;
	text-align: left;
}
.editAddress label i {
	font-style: normal;
	font-size: .6rem;
}
.editAddress .weui-cells {
	font-size: .6rem;
	color: #666;
}
.editAddress .weui-cells span.vux-popup-picker-value {
	width: 75%;
	display: inline-block;
	/**/
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}
.editAddress .cont input {
	outline: none;
	color: #333;
	height: 1rem;
	border: none;
	float: left;
	font-size: .6rem;
	margin-top: .21rem;
}
.editAddress .weui-cell {
	padding: 0px 0px;
}
.editAddress .task .weui-cell_access .weui-cell__ft:after {
	display: none;
}
.editAddress .weui-cells:before {
	display: none;
}
.editAddress .weui-cells:after {
	display: none;
}
.editAddress .vux-cell-box:before {
	display: none;
}
.editAddress .add-info {
	display: inline-block;
	width: 100%;
	height: 100%;
	margin-top: -.21rem;
}
.editAddress .add-info textarea {
	float: left;
	font-size: .6rem;
	line-height: 1rem;
	margin-left: 4.32rem;
	margin-top: -.8rem;
	border: 1px #EEEEEE solid;
	overflow: hidden;
	resize: none;
	width: 9.13rem;
	height: 4.34rem;
}
.editAddress .pageTitle .block-pic {
	display: inline-block;
	width: 2rem;
	height: 2rem;
	background-repeat: no-repeat;
	background-size: 100%;
	float: left;
	margin-left: -.34rem;
}
.editAddress .pageTitle .goBack {
	background-image: url(../../imgs/go-back.png);
}
.editAddress .iconPic {
	display: inline-block;
	width: 1.1rem;
	height: 1.1rem;
	background-repeat: no-repeat;
	background-size: 100%;
	margin-bottom: -.21rem;
	margin-right: .21rem;
}
.editAddress .namePic {
	background-image: url(../../imgs/urName.png);
}
.editAddress .phonePic {
	background-image: url(../../imgs/urPhone.png);
}
.editAddress .addPic {
	background-image: url(../../imgs/urAdd.png);
}
.editAddress .infoPic {
}



