
@important "../../reset.css";
.search-all  ul.com-nav {
	left: 0;
	right: 0;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	background: #fff;
	border-bottom: 0.1rem solid #eeeeee;
	line-height: 2rem;
	height: 2rem;
	position:fixed;
	z-index: 99;
	margin-top: 46px;
}
.search-all .block  .cat_title {
	
	text-align: center;
	font-size: 15px;
	color:#000000;
	position: absolute;
	top: 38px;
	height: 40px;
	line-height: 40px;
	background-color: #FFFFFF;
	width: 100%;
	margin: 10px 0;
	border-bottom: 1px #eeeeee solid;
}
.search-all  ul.com-nav>li {
	text-align: center;
	display: block;
	-webkit-flex: 1!important;
	-webkit-box-flex: 1;
}
.search-all  ul.com-nav>li>a {
	font-size: .56rem;
	color: #232323;
	display: inline-block;
	position: relative;
	line-height: 1.32rem;
	height: 1.32rem;
}
.search-all  ul.com-nav>li.active>a {
	color: #eb6100;
}
.search-all  ul.com-nav>li>a>i {
	position: relative;
	width: .4rem;
	height: .46rem;
	display: inline-block;
	margin-left: .12rem;
	top: 0.06rem;
}
.search-all  ul.com-nav>li>a>i:before {
	background: url(../imgs/ico_sort.png) no-repeat;
	background-size: 100% auto;
	position: absolute;
	left: 0;
	content: '';
	display: inline-block;
	width: .4rem;
	height: 0.2rem;
	bottom: 0;
	background-position: 0 -0.98rem;
}
.search-all  ul.com-nav>li>a>i.down:before {
	background-position: 0 bottom;
}
.search-all  ul.com-nav>li>a>i:after {
	background: url(../imgs/ico_sort.png) no-repeat;
	background-size: 100% auto;
	position: absolute;
	left: 0;
	content: '';
	display: inline-block;
	width: .4rem;
	height: 0.2rem;
	top: 0;
	background-position: 0 0;
}
.search-all  ul.com-nav>li>a>i.up:after {
	background-position: 0 -0.48rem;
}
.search-all  article.active_block {
	visibility: visible;
	display: block;
	z-index: 1;
	width: 100%;
	height: 90vh;
	top:14vh;
	position: absolute;
	box-sizing: border-box;
	background-color: #eee;
}
.search-all .block article.active_block p.null-txt {
	font-size: .6rem;
	color: #999;
	text-align: center;
}
.search-all .block article.active_block i.null-addr {
	width: 4.8rem;
	height: 4rem;
	background: url(../imgs/ps_gray_logo.png) no-repeat;
	background-size: 4.8rem auto;
	display: block;
	margin: 4.4rem auto 1.34rem;
}
.search-all .block article.active_block .go-list {
	margin-bottom: 2rem;
}
.search-all .block article.active_block .go-list>li {
	height: 7.16rem;
	position: relative;
	background: #fff;
	border-bottom: 1px solid #f1f1f1;
}
.search-all .block article.active_block a.del-items {
	display: block;
	height: 7.16rem;
	position: relative;
	border-bottom: 1px solid #eee;
	padding-top: 0px;
}
.search-all .block article.active_block a.car {
	width: 1.76rem;
	height: 1.76rem;
	background: url(../imgs/cart_pic1.png) no-repeat;
	background-position: 6px 7px;
	background-size: 1.1rem;
	display: inline-block;
	position: absolute;
	top: 3.8rem;
	right: 1.2rem;
	border: 2px solid #FD4433;
	background-color: #FD4433;
	border-radius: 1rem;
}
.search-all .block article.active_block div.car_shadow {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, .06);
	z-index: 999;
}
.search-all .block article.active_block div.car_shadow .shadow_cont {
	background-color: #fff;
	width: 80%;
	margin-left: auto;
	margin-right: auto;
	margin-top: 20vh;
	position: relative;
}
.search-all .shadow_cont .shadow_title {
	position: relative;
	padding: 0 .96rem;
	margin-bottom: 1.4rem;
	border-bottom: 1px solid #cccccc;
	text-align: left;
}
.search-all .shadow_cont .shadow_title p {
	padding-top: 1.04rem;
	font-size: 0.6rem;
}
.search-all .shadow_cont .shadow_title span {
	height: 1.2rem;
	padding-top: .5rem;
	line-height: .76rem;
	margin-bottom: 0.54rem;
	font-size: .96rem;
	color: #eb6100;
	line-height: .64rem;
	margin-left: -0.12rem;
	display: block;
}
.search-all .shadow_cont .shadow_title .close_btn {
	position: absolute;
	top: .6rem;
	right: .4rem;
	width: .48rem;
	height: .48rem;
	display: block;
	background: url(../imgs/pop-close.png) no-repeat;
	background-size: .48rem auto;
	margin: -0.24rem 0 0 -0.24rem;
}
.search-all .shadow_cont .shadow_spec {
	text-align: left;
	padding: 0 .96rem 1.76rem .96rem;
}
.search-all .shadow_cont .shadow_spec p.sp {
	padding-bottom: 1rem;
	overflow: hidden;
}
.search-all .shadow_cont .shadow_spec p.sp label {
	display: block;
	width: 2.48rem;
	-webkit-box-flex: 1;
	font-size: 0.48rem;
}
.search-all .shadow_cont .shadow_spec p.sp span {
	display: inline-block;
	font-size: .48rem;
	height: 1rem;
	padding: 0 .26rem;
	min-width: 2rem;
	text-align: center;
	line-height: 1rem;
	display: inline-block;
	float: left;
	margin-right: .4rem;
	margin-bottom: 0.2rem;
	border: 1px solid #ccc;
}
.search-all .shadow_cont .shadow_spec p.sp span.act {
	border-color: #eb6100;
	background: #eb6100;
	color: #ffffff;
}
.search-all .shadow_cont .shadow_spec p.g_num label {
	width: 2.48rem;
	-webkit-box-flex: 1;
	font-size: 0.48rem;
	display: block;
}
.search-all .shadow_cont .shadow_spec p.g_num span {
	overflow: hidden;
	display: block;
	margin-bottom: 2rem;
}
.search-all .shadow_cont .shadow_spec p.g_num span i {
	font-style: normal;
	color: #232323;
	box-shadow: none;
	float: left;
	width: 1rem;
	height: 1rem;
	line-height: 1rem;
	font-size: .6rem;
	border: 1px solid #999;
	background: #FFF;
	text-align: center;
	margin: 0 .08rem;
	border-radius: 0;
}
.search-all .shadow_cont .shadow_spec p.g_num span em {
	float: left;
	display: inline-block;
	width: 1rem;
	height: 1rem;
	background: #999;
	text-align: center;
	color: #fff;
}
.search-all .shadow_cont .shadow_spec p.g_num span em.act {
	background-color: #ccc;
}
.search-all .shadow_cont a.addCar {
	width: 100%;
	padding: 0 .3rem;
	line-height: 1.76rem;
	font-size: 0.64rem;
	color: #fff;
	background: #eb6100;
	display: inline-block;
	border: none;
	cursor: pointer;
	text-align: center;
	position: absolute;
	bottom: 0;
	left: 0;
}
.search-all .block article.active_block .go-item {
	background: #fff;
	padding: 0 23px;
	position: relative;
	height: 4.72rem;
}
.search-all .block article.active_block .go-item>img {
	width: 5.8rem;
	height: 5.8rem;
	position: relative;
	top: 0.5rem;;
	float: left;
}
.search-all .block article.active_block .go-item .info_bar {
	background: #fff;
	color: #000;
	width: 6.64rem;
	float: right;
}
.search-all .block article.active_block .go-item .info_bar .tit {
	font-size: .64rem;
	display: -webkit-box;
	height: 1.76rem;
	line-height: .9rem;
	margin-top: .86rem;
	text-align: left;
}
.search-all .block article.active_block .go-item .info_bar .price {
	margin-top: 1.7rem;
	margin-left: -.16rem;
	text-align: left;
	font-size: .96rem;
}
.search-all span.pic {
	display: inline-block;
	width: 1.2rem;
	height: 1.4rem;
	background-image: url(../assets/search.png);
	background-repeat: no-repeat;
	background-size: cover;
}
.search-all .header {
	left: 0;
	right: 0;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	background-color: #FFFFFF;
	border-bottom: 0.1rem solid #eeeeee;
	line-height: 2rem;
	height: 2rem;
	position:fixed;
	z-index: 99;
}
.search-all .close-pic {
	width: 30px;
	height: 30px;
	float: left;
	background-image: url(../imgs/close.png);
	background-repeat: no-repeat;
	margin-left: 25px;
	margin-top: 10px;
}
.search-all .input {
	width: 86%;

	position: relative;
}
.search-all input {
	border-radius: 5px;
	border: 1px solid #ccc;
	height: 1.4rem;
	width: 90%;
	padding-left: 5px;
	position: absolute;
	left: 0;
	top: .3rem;
	color: #ccc;
}
.search-all .search-pic {
	display: inline-block;
	width: 1.4rem;
	height: 1.4rem;
	background-color: #F5F5F5;
	position: absolute;
	right: 10%;
	top: .3rem;
	border: 1px solid #ccc;
	border-top-right-radius: 5px;
	border-bottom-right-radius: 5px;
}
.search-all .search-pic>span {
	background-position: -32px;
}



