
@import "../../reset.css";
@import '../../static/swiper.min.css';
body {
	background-color: #fff;
}
.serve .swiper-wrapper {
	height: auto;
}
.serve .ps_class_bigger {
	width: 100%;
	position: absolute;
	bottom: 0;
	top: 0rem;
	background-color: #eee;
	z-index: 3;
	float: left;
	margin-top: 46px;
}
.serve .vux-header .vux-header-left a,
.vux-header .vux-header-left button,
.vux-header .vux-header-right a,
.vux-header .vux-header-right button {

	color: white;
}
.serve .vux-header .vux-header-left .left-arrow:before {

	border: 1px solid white;
	content: "";
	position: absolute;
	width: 12px;
	height: 12px;
	border-width: 1px 0 0 1px;
	-webkit-transform: rotate(315deg);
	transform: rotate(315deg);
	top: 8px;
	left: 7px;
}
.serve .left_bar {
	float: left;
	width: 25%;
	min-height: 80vh;
	background-color: #fff;
	overflow: hidden;
}
.serve .left_bar .left_item {
	height: 2rem;
	line-height: 2rem;
	text-align: center;
	box-sizing: border-box;
	border-bottom: 1px solid #F2F2F2;
	border-right: 1px solid #F2F2F2;
	font-size: 0.6rem;
}
.serve .left_bar .left_item.li_active {
	background-color: #F3F5F7;
	/*	border: none !important;*/
	color: #f00;
}
.serve .right_bar {
	background: #F3F5F7;
	overflow-y: scroll;
	font-size: 13px;
}
.serve .right_bar .class_second {
	clear: both;
	font-size: 0.6rem;
	font-weight: 500;
	text-align: center;
	float: left;
	margin: 15px 85px;
	border-bottom: 1px #EEEEEE solid;
}
.serve .weui-grid__icon {
	width: 70px;
	height: 70px;
	margin-bottom: 5px;
}
.serve .weui-grid {
	padding: 15px 10px;
}
.serve .weui-grid:before {
	border: none;
}
.serve .weui-grid:after {
	border: none;
}



