
@import '../../../static/swiper.min.css';
.store-list {
	background-color: #fff;
}
.store-list .swiper-container {
	background-color: #ffffff;
}
.store-list .swiper_img {
	height: 7rem;
	width: 100%;
	display: block;
}
.store-list .swiper-container .swiper-slide {
	position: relative;
}
.store-list .vux-header .vux-header-left a,
.vux-header .vux-header-left button,
.vux-header .vux-header-right a,
.vux-header .vux-header-right button {

	color: white;
}
.store-list .vux-header .vux-header-left .left-arrow:before {

	border: 1px solid white;
	content: "";
	position: absolute;
	width: 12px;
	height: 12px;
	border-width: 1px 0 0 1px;
	-webkit-transform: rotate(315deg);
	transform: rotate(315deg);
	top: 8px;
	left: 7px;
}
.store-list .swiper-container .info {
	position: absolute;
	left: 12%;
	top: 2.5rem;
	display: inline-block;
	width: 76%;
	height: 3.2rem;
	background-color: rgba(255, 255, 255, .8);
	padding: .55rem;
}
.store-list .swiper-container .info span {
	display: block;
	font-size: .3rem;
}
.store-list .swiper-container .info .info-mess {
	font-size: 1rem;
}
.store-list .site {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	margin-top: .1rem;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	padding: 1%;
}
.store-list .site-block {
	width: 30%;
	-webkit-box-flex: 1;
	    -ms-flex-positive: 1;
	        flex-grow: 1;
	position: relative;
	padding: 1%;
}
.store-list .site-block img {
	width: 100%;
	height: 100%;
}
.store-list .site-block p {
	position: absolute;
	left: 0;
	bottom: 0;
	background-color: rgba(255, 255, 255, .8);
	width: 100%;
	font-size: .4rem;
	color: #666;
	padding: .1rem;
}
.store-list .site-block p span {
	display: block;
	font-size: .8rem;
	color: #333;
}
.store-list .brand {
	overflow: hidden;
	border-left: 1px solid #eeeeee;
	width: 97%;
	margin-left: auto;
	margin-right: auto;
}
.store-list .brand .item {
	width: 25%;
	float: left;
	border-top: 1px solid #eeeeee;
	border-bottom: 1px solid #eeeeee;
	border-right: 1px solid #eeeeee;

	box-sizing: border-box;
	height: 88px;
	padding: 10px;
}
.store-list .brand .item:nth-child(3n) {
	border-right: 1px solid #eee;
}
.store-list .brand .item img {
	width: 60px;
	height: 60px;
}
.store-list .pic-adv {
	overflow: hidden;
	margin-top: .5rem;
	background-color: #F2F2F2;
	padding-bottom: .3rem;
}
.store-list .pic-adv .item {
	width: 100%;
	height: 10rem;
	display: block;
	margin-bottom: .6rem;
	background-color: #fff;
	text-align: left;
}
.store-list .pic-adv .item:last-child {
	margin-bottom: 0;
}
.store-list .pic-adv .item a {
	display: block;
}
.store-list .pic-adv .item img {
	width: 100%;
	height: 8rem;
	float: left;
}
.store-list .pic-adv .item p {
	padding: 0 .6rem;
	font-size: .6rem;
	border-top: 1px solid #F5F5F5;
	border-bottom: 1px solid #F5F5F5;
	width: 100%;
	height: 2rem;
	line-height: 2rem;
	float: left;
}
.store-list .pic-adv .item .title {
	color: #f00;
	margin-right: .2rem;
}
.store-list .pic-adv .item .time {
	float: right;
}



