
.ps-detail-content1{
}
/**/
.product-detail .ps_pro_img .swiper-wrapper img {
  height: 10rem;
  width: 350px;
}
/**/
.product-detail .detail-con {
  color: #232323;
  padding: .8rem .64rem .88rem .64rem;
  text-align: left;
}
.product-detail .detail-con .title-wrap h1 {
  font-size: .64rem;
  color: #232323;
}
.product-detail .detail-con .title-wrap h2 {
  -webkit-line-clamp: 1;
  font-size: .52rem;
  line-height: 1.26;
  color: #999;
}
.product-detail .detail-con .title-wrap .text-info {
  padding-top: .54rem;
  height: 1.2rem;
  line-height: .66rem
}
.product-detail .detail-con .title-wrap .text-info .price {
  font-size: 1rem;
  line-height: .64rem;
  text-indent: -.12rem
}
.product-detail .detail-con .title-wrap .text-info .other-price {
  text-decoration: line-through;
}
.product-detail .detail-con .title-wrap .original-price {
  height: 1.14rem;
  font-size: .72rem;
  color: #999;
  line-height: .84rem;
  margin-top: .36rem;
  border-bottom: 0.04rem solid #eee;
}





/**/
.product-detail .main-det-con {
  position: relative;
  background-color: #eee;
  padding-bottom: 0.3rem;
}
.product-detail .pro-parameter {
  overflow: hidden;
  text-align: left;
}
.product-detail .pro-parameter .bd {
  padding: 1.08rem .52rem 1.12rem;
  background: #fff;
  border-top: .12rem solid #eee;
  border-bottom: .12rem solid #eee;
  color: #999;
  font-size: .48rem
}
.product-detail .pro-parameter dt {
  font-size: .52rem;
  width: 100%;
  /*float: left;*/
  /*padding-bottom: .36rem*/
}
.product-detail .detail-content p img {
  display: block;
  width: 100%;
}





/**/
.product-detail .foorer-nav>a,
.product-detail .foorer-nav .watch_out {
  border-radius: 0;
  padding: 0!important;
  height: 1.96rem;
  position: relative;
  -webkit-box-flex: 1;
  box-flex: 1;
  display: block;
  color: #777;
  text-align: center;
  font-size: 0.6rem;
}
.product-detail .foorer-nav>a {
  line-height: 3rem;
  width: 8%;
}
.product-detail .foorer-nav .button {
  width: 5.72rem;
  line-height: 1.96rem;
  color: #fff;
}
.product-detail i.attion_pic {
  position: absolute;
  left: 33%;
  content: '';
  width: 30px;
  height: 30px;
  background: url(../imgs/like1.png) no-repeat;
  margin-top: -2px;
}
.product-detail i.attion_pic1 {
  position: absolute;
  left: 33%;
  content: '';
  width: 30px;
  height: 30px;
  background: url(../imgs/like.png) no-repeat;
  margin-top: -2px;
}
.product-detail .footer-car {
  width: 2.36rem;
  height: 1.96rem;
  border-width: 0.04rem;
  /*border-style: solid;*/
  border-color: rgba(153, 153, 153, 0.3);
  border-top: 0;
  border-bottom: 0;
}
.product-detail .footer-car i {
  width: .9rem;
  height: .9rem;
  /* background-position: 0 -3.94rem; */
  position: absolute;
  top: 0.6rem;
  left: 50%;
  margin: -.44rem 0 0 -.42rem;
  background: url(../imgs/cart_pic.png) no-repeat;
  background-size: cover;
}
.product-detail .footer-car em {
  width: .9rem;
  height: .9rem;
  /* background-position: 0 -3.94rem; */
  position: absolute;
  top: 0.6rem;
  left: 50%;
  margin: -.44rem 0 0 -.42rem;
  background: url(../imgs/shop.png) no-repeat;
  background-size: cover;
}
.product-detail .orange {
  background-color: #eb6100 !important;
}





/**/
.product-detail .weui-cell__ft {
  display: none !important;
}
.product-detail .weui-dialog__ft {
  display: none !important;
}
.product-detail .weui-dialog {
  width: 40%;
}
.product-detail .weui-cell {
  padding: 0;
}
.product-detail .weui-cells:before,
.product-detail .weui-cell:before {
  border: none !important;
}
.product-detail .weui-cells:after {
  border: none !important;
}
.product-detail .vux-label {
  text-align: center;
}
.product-detail .vux-label {
  margin-top: 0.8rem;
  color: #777;
  margin-left: 2px;
}
.product-detail .swiper-pagination-bullet-active{
	background-color: #EB6100;
}
.product-detail .car_shadow {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, .6);
		z-index: 999;
}
.product-detail .shadow_cont {
		background-color: #fff;
		width: 80%;
		margin-left: auto;
		margin-right: auto;
		position: absolute;
    top: 165px;
    left: 38px;
}
.product-detail .shadow_cont .shadow_title {
		position: relative;
		padding: 0 .96rem;
		margin-bottom: 1.4rem;
		border-bottom: 1px solid #cccccc;
		text-align: left;
}
.product-detail .shadow_cont .shadow_title p {
		padding-top: 1.04rem;
		font-size: 0.6rem;
}
.product-detail	 .shadow_cont .shadow_title span {
		height: 1.2rem;
		padding-top: .5rem;
		line-height: .76rem;
		margin-bottom: 0.54rem;
		font-size: .96rem;
		color: #eb6100;
		line-height: .64rem;
		margin-left: -0.12rem;
		display: block;
}
.product-detail	 .shadow_cont .shadow_title .close_btn {
		position: absolute;
		top: .6rem;
		right: .4rem;
		width: .48rem;
		height: .48rem;
		display: block;
		background: url(../imgs/pop-close.png) no-repeat;
		background-size: .48rem auto;
		margin: -0.24rem 0 0 -0.24rem;
}
.product-detail	 .shadow_cont .shadow_spec {
		text-align: left;
    padding: 0 1.96rem 0.00rem .96rem;
}
.product-detail	 .shadow_cont .shadow_spec p.sp {
		padding-bottom: 1rem;
		overflow: hidden;
}
.product-detail	 .shadow_cont .shadow_spec p.sp label {
		display: block;
		width: 2.48rem;
		-webkit-box-flex: 1;
		font-size: 0.48rem;
}
.product-detail	 .shadow_cont .shadow_spec p.sp span {
		display: inline-block;
		font-size: .48rem;
		height: 1rem;
		padding: 0 .26rem;
		min-width: 2rem;
		text-align: center;
		line-height: 1rem;
		display: inline-block;
		float: left;
		margin-right: .4rem;
		margin-bottom: 0.2rem;
		border: 1px solid #ccc;
}
.product-detail	 .shadow_cont .shadow_spec p.sp span.act {
		border-color: #eb6100;
		background: #eb6100;
		color: #ffffff;
}
.product-detail	 .shadow_cont .shadow_spec p.g_num label {
		width: 2.48rem;
		-webkit-box-flex: 1;
		font-size: 0.48rem;
		display: block;
}
.product-detail	 .shadow_cont .shadow_spec p.g_num span {
		overflow: hidden;
		display: block;
		margin-bottom: 2rem;
}
.product-detail	 .shadow_cont .shadow_spec p.g_num span i {
		font-style: normal;
		color: #232323;
		box-shadow: none;
		float: left;
		width: 1rem;
		height: 1rem;
		line-height: 1rem;
		font-size: .6rem;
		border: 1px solid #999;
		background: #FFF;
		text-align: center;
		margin: 0 .08rem;
		border-radius: 0;
}
.product-detail	 .shadow_cont .shadow_spec p.g_num span em {
		float: left;
		display: inline-block;
		width: 1rem;
		height: 1rem;
		background: #999;
		text-align: center;
		color: #fff;
		line-height: 1rem;
}
.product-detail	 .shadow_cont .shadow_spec p.g_num span em.act {
		background-color: #ccc;
}
.product-detail	 .shadow_cont a.addCar {
		width: 100%;
		padding: 0 .3rem;
		line-height: 1.76rem;
		font-size: 0.64rem;
		color: #fff;
		background: #eb6100;
		display: inline-block;
		border: none;
		cursor: pointer;
		text-align: center;
		
		bottom: 0;
		left: 0;
}





