
@import "../../reset.css";
.themeInfo .header{
	height: 2rem;
	background-color:#FFFFFF;
	position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 99;
    line-height: 2rem;
    font-size: 16px;
    border-bottom: 1px #EEEEEE solid;
    padding-right: 20px;
    text-align: center;
}
.themeInfo .themeBanner{
	height: 230px;
	margin-bottom: 20px;
}
.themeInfo .themeBanner img{
	width: 100%;
	height: 230px;
	border-bottom: 1px #EEEEEE solid;
}
.themeInfo .themeSelect{
	height: 40px;
	border-bottom: 2px #EEEEEE solid;
}
.themeInfo .themeSelect .selectItem{
	width: 80px;
	height: 100%;
	float: left;
	line-height: 40px;
	color: #000000;
}
.themeInfo .selectTitle{
	height: 40px;
	border-bottom: 1px #EEEEEE solid;
	line-height: 40px;
	font-size: 15px;
	margin-bottom: 10px;
}
.themeInfo ul{
	border-bottom: 1px #EEEEEE solid;
}
.themeInfo .active{
	border-bottom: 1px #000000 solid;
	width: 20px;
}




