
@import "../../reset.css";
.theme .themeList{
	background-color: #0E90D2;
	margin-top: 2rem;
}
.theme .themeItem{
	height: 230px;
	border-bottom: 1px #000000 solid;
}
.theme .themeItem img{
	width: 100%;
	height: 230px;
}
.theme .themeItem .themeTxt{
	position: absolute;
    margin-top: -54px;
    left: 0;
    display: block;
    width: 100%;
    height: 2rem;
    line-height: 1.4rem;
    background: rgba(0, 0, 0, .5);
    color:#FFFFFF;
    text-align: left;
    padding-left: .4rem;
    font-size: .6rem;
}
.theme .themeTxt h5{
	font-weight: normal;
    font-size: 15px;
}
.theme .themeTxt h4{
	font-weight: normal;
    margin-top: -13px;
}
.vux-header .vux-header-left .left-arrow:before {
    content: "";
    position: absolute;
    width: 12px;
    height: 12px;
    border: 1px solid white;
    border-width: 1px 0 0 1px;
    -webkit-transform: rotate(315deg);
    transform: rotate(315deg);
    top: 8px;
    left: 7px;
}
.vux-header .vux-header-left a {
    color: white;
}



