
.footprint .ps_love_outer {
  background: url(../../imgs/ps_maybe_love.png) no-repeat;
  background-size: 100% 100%;
  height: 1.7rem !important;
  margin: 0.72rem 0 0.72rem 0;
}
.footprint .pro-list {
  background: #fff;
  padding-bottom: 0.5rem;
  overflow: hidden;
}
.footprint .pro-list li {
  float: left;
  overflow: hidden;
  margin-top: .2rem;
  width: 45%;
  height: 7.4rem;
  position: relative;
  margin-left: 0.77rem;
  margin-bottom: 15px;
}
.footprint .pro-list li:nth-child(even) {
  margin-left: 0;
  margin-right: 0.77rem;
}
.footprint .pro-list li img {
  width: 5.2rem;
  height: 5.2rem;
  display: block;
  margin: 0 auto;
  color: #000000;
}
.footprint .vux-header .vux-header-left a,
.vux-header .vux-header-left button,
.vux-header .vux-header-right a,
.vux-header .vux-header-right button {
  color: white;
}
.footprint .vux-header .vux-header-left .left-arrow:before {
  border: 1px solid white;
  content: "";
  position: absolute;
  width: 12px;
  height: 12px;
  border-width: 1px 0 0 1px;
  -webkit-transform: rotate(315deg);
  transform: rotate(315deg);
  top: 8px;
  left: 7px;
}
.footprint .pro-list li a {
  margin: 0 .5rem;
  position: relative;
  height: 100%;
  display: block;
}
.footprint .pro-list li .info-bar {
  position: absolute;
  left: 1.7rem;
  bottom: -1rem;
  height: 2.62rem;
  width: 64%;
  background: #ffffff;
}
.footprint .pro-list li .info-bar:nth-child(odd) {
  left: 0;
  right: 1.7rem;
}
.footprint .pro-list .info-bar .tit {
  font-size: .48rem;
  height: .68rem;
  line-height: .68rem;
  margin: .2rem 0 0;
  overflow: hidden;
  text-align: left;
}
.footprint .pro-list .info-bar .price {
  font-size: 0.56rem;
  display: block;
  text-align: left;
}
.footprint .pro-list .info-bar .price i {
  padding-right: 0.24rem;
}



