
.newActivity .activityList {
	padding: .5rem;
}
.newActivity .activityItem {

	height: 8.5rem;
	overflow: hidden;
	padding: 10px 10px;
	border: 1px solid #eee;
	border-radius: 20px;
	margin-bottom: 20px;
	box-shadow: 3px 3px 4px #eee
}
.newActivity img {
	width: 5rem;
	height: 5rem;
	margin-top: .5rem;
	float: left;
}
.newActivity .activityTxt {
	margin-left: .5rem;
	margin-top: 1rem;
	float: left;
	width: 8.5rem;
	-webkit-line-clamp: 2;
	overflow: hidden;

	text-align: left;
}
.newActivity h1 {
	font-weight: 500;
	font-size: 18px;
	overflow: hidden;
	margin-top: -5px;
}
.newActivity b {
	font-weight: 500;
	font-size: 16px;
	width: 6rem;
	overflow: hidden;
	float: left;
	margin-top: 5px;
}
.newActivity em {
	font-size: 16px;
	float: left;
	margin-left: 20px;
	margin-top: 10px;
	color: #eb6100;
}
.newActivity h3 {
	margin-top: 10px;
	font-size: 14px;
	float: left;
	text-align: left;
	display: -webkit-box;
	-webkit-line-clamp: 1;
	-webkit-box-orient: vertical;
	overflow: hidden;
	color: #BFBFBF;
}
.newActivity .activityBtn {
	float: left;
	width: 100%;
	overflow: hidden;
	padding-left: 8.5rem;
}
.newActivity .btn {
	width: 100px;
	border: 1px #eb6100 solid;
	float: left;
	margin-right: 30px;
	height: 40px;
	line-height: 40px;
	border-radius: 15px;
	color: #FFFFFF;
	background-color: #eb6100;
	border: none;
}



