
@import "../../reset.css";
@import '../../static/swiper.min.css';
body {
	background-color: #fff;
}
.category .swiper-wrapper {
	height: auto;
}
.category .ps_class_bigger {
	width: 100%;
	position: absolute;
	bottom: 0;
	top: 2rem;
	background-color: #eee;
	z-index: 3;
	float: left;
}
.category .left_bar {
	float: left;
	width: 25%;
	min-height: 80vh;
	background-color: #fff;
	padding-bottom: 2rem;
	overflow: hidden;
}
.category .left_bar .left_item {
	height: 2rem;
	line-height: 2rem;
	text-align: center;
	box-sizing: border-box;
	border-bottom: 1px solid #F2F2F2;
	border-right: 1px solid #F2F2F2;
	font-size: 0.6rem;
}
.category .left_bar .left_item.li_active {
	background-color: #F3F5F7;
	/*	border: none !important;*/
	color: #eb6100;
}
.category .right_bar {
	background: #F3F5F7;
	overflow-y: scroll;
	padding-bottom: 0.5rem;
	font-size: 13px;
}
.category .weui-grid__icon {
	width: 70px;
	height: 70px;
	margin-bottom: 5px;
}
.category .weui-grid {
	padding: 15px 10px;
}
.category .weui-grid:before {
	border: none;
}
.category .weui-grid:after {
	border: none;
}
.category .weui-grids:after {
	border: none;
}



