.myself .ps-detail-content {
  background-color: #F0F2F5;
  margin-top: 0px;
  z-index: 1;
}
.myself .center {
  text-align: center;
  padding-top: 1rem;
  color: #fff;
}
.myself .center img {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  border: 2px solid #ececec;
  margin-top: 1rem;
}
.top-setting {
  position: absolute;
  right: 0;
  top: 1rem;
  height: 1.2rem;
  width: 3rem;
  border: 1.5px solid white;
  border-right: none;
  border-radius: .52rem 0 0 .52rem;
}
.setting-text {
  display: inline-block;
  font-size: .6rem;
  color: white;
  padding-left: .5rem;
  padding-top: .04rem;
}
.myself .username {
  font-size: .7rem;
  color: white;
}
.myself .card-demo-flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.myself .card-demo-content01 {
  padding: 10px 0;
}
.myself .card-demo-flex > div {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  font-size: .55rem;
}
.myself .card-demo-flex span {
  color: #EB6100;
}
.myself .myself-item {
  margin-top: .5rem;
}
.myself .myself-item a {
  display: block;
  width: 100%;
  background-color: #fff;
  height: 2rem;
  line-height: 2rem;
  border-bottom: 1px solid #eee;
  text-align: left;
  padding-left: .5rem;
  box-sizing: border-box;
}
.myself .myself-item span {
  display: inline-block;
  line-height: 2rem;
  height: 2rem;
  margin-left: .5rem;
}
.myself .myself-item a i {
  background-image: url(../../assets/myself.png);
  display: inline-block;
  width: .8rem;
  height: 1rem;
  margin-top: .5rem;
  background-repeat: no-repeat;
  background-size: cover;
  float: left;
}
i.order {
  background-position: -3.3rem 0;
}
i.address {
  background-position: -4.1rem 0;
}
i.collect {
  background-position: -1.6rem 0;
}
i.coupon {
  background-position: -2.4rem 0;
}
i.footprint {
  background-position: 0.03rem 0;
}
i.feedback {
  background-position: -0.7rem 0;
}
.myself .myself-item a em.right-pic {
