
.activity{
	min-height: 100vh;
	background-color: #fff;
	padding: .6rem;
}
.activity .item{
	position: relative;
	padding-top:1rem ;
	margin-top: 40px;
}
.activity .item img{
	width: 100%;
}
.activity .item span{
	position: absolute;
	left: 0;
	top: 0;
	display: inline-block;
	padding: 0 .2rem;
	background-color: #FF4500;
	color: #fff;
	height: 1rem;
	font-size: .6rem;
}



