
.fl {
	float: left;
}
.rl {
	float: right;
}
.day .themeList {

	margin: 10px;
}
.day .themeItem {
	background-color: #ffffff;
	height: 280px;
}
.day img {
	width: 100%;
	height: 150px;
}
.day .themeTxt {
	padding: 5px 0px;
}
.day h4 {
	margin-top: 5px;
	font-size: 23px;
	color: #000000;
}
.day .themeInfo {
	margin-top: 10px;
	text-align: center;
	overflow: hidden;
	display: -webkit-box;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
	font-size: 15px;
	padding: 0 10px;
	color: #BFBFBF;
}
.day .like {
	margin-right: 10px;
}
.day .like .icon {
	width: 28px;
	height: 28px;
	background: url(../imgs/haha.png);
}
.day .like i {
	display: inline-block;
	background-repeat: no-repeat;
	background-size: 100%;
	margin-right: 8px;
}
.day .like b {
	margin-top: 3px;
	float: left;
	font-weight: 500;
	font-size: 17px;
	color: #db0000;
}
.day .title {
	width: 100%;
	height: 150px;
	position: absolute;
	display: inline-block;
	color: #FFFFFF;
	font-size: 23px;
	background-color: rgba(0, 0, 0, 0.3);
}
.day .title  div {
	padding-top: 50px;
}
.day .title em {
	display: inline-block;
	width: 55px;
	height: 50px;
	background: url(../imgs/open.png);
	background-repeat: no-repeat;
	background-size: 100%;
}



