
@import "../../reset.css";
.car .null-car {
	height: 15rem;
	padding-top: 3rem;
	text-align: center;
	background-color: #EEEEEE;
}
.car .null-car .ico {
	background: url(../imgs/null1.png) no-repeat;
	width: 4.74rem;
	height: 4.74rem;
	background-size: 4.74rem auto;
	display: block;
	margin: 0 auto .68rem;
}
.car .null-car p {
	color: #999;
	font-size: .6rem;
}
.car .null-car .button {
	font-size: .6rem;
	display: inline-block;
	margin-top: 1.04rem;
	border: 1px solid #eb6100;
	color: #fff;
	background: 0 0;
	background-color: #eb6100;
	padding: 0.2rem 0.9rem;
}
.car .car_list {
}
.car .car_list .carItem {


	padding: 15px 10px;
	overflow: hidden;

	background-color: #ffffff;
}
.car .carItem .pro-check {
	float: left;
	position: absolute;
	margin-top: 30px;
	width: 8%;


	height: 100%;
}
.car .carItem a {

	width: 90%;
	float: left;
	margin-left: 10px;
}
.car .carItem img {
	width: 3.6rem;
    height: 3.6rem;
    float: left;
    margin-left: 30px;
}
.car .carItem .goodsInfo {
	float: left;
	text-align: left;
	width: 8.9rem;
}
.car .goodsInfo h3 {
	font-weight: normal;
	font-size: .66rem;
}
.car .goodsInfo h4 {
	font-weight: normal;
	font-size: .36rem;
	color: #999;
}
.car .goodsInfo .goodsInfoBottom {
	bottom: 15px;
    position: absolute;
	font-size: .66rem;
	color: #eb6100;
}
.car .goodsInfoBottom b {
	font-weight: normal;
	position: absolute;
    left: 200px;
    bottom: 0px;
	color: #000000;
}
.car .foorer-nav1 {
	background-color: #EB6100;
	bottom: 1.96rem;
	position: fixed;
	z-index: 99;
	width: 100%;
}
.car .foorer-nav1 .bot-price {
	width: 8rem;
	font-size: .48rem;
	height: 45px;
	overflow: hidden;
	background-color: #FFFFFF;
	position: relative;
	text-align: center;
	display: inline-block;
	float: left;
	border-top: 1px #eeeeee solid;
}
.car.foorer-nav1.car .bot-price em {
	color: #ffffff;
}
.car .Jcheckall {
	position: absolute;
	left: .8rem;
	top: 50%;
	margin-top: -0.15rem;
	width: 80px;

	text-align: left;
}
.car .foorer-nav1 .bot-price .JAllPrice {
	color: #eb6100;
	font-size: .72rem;
	height: .82rem;
	line-height: 1.1rem;
	padding-right: .36rem;
	text-align: right;
	margin-top: 3px;
}
.car .foorer-nav1 .bot-price .JAllPrice+div {
	line-height: .7rem;
	padding-right: .36rem;
	text-align: right;
}
.car .header {
	height: 2rem;
	background-color: #ffffff;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 99;
	line-height: 2rem;
	font-size: 18px;
	border-bottom: 1px #EEEEEE solid;
	padding-right: 20px;
	text-align: center;
}
.car .header span {
	margin-left: 20px;
}
.car .header em {
	height: 2rem;

	line-height: 2rem;
	font-size: 15px;
	float: right;
}
.car .carItem .goodsEdit {

	float: left;
	text-align: left;
	width: 8.9rem;
}
.car .goodsEdit {
	display: none;
}
.car .goodsEdit .count {
	border: 1px #EEEEEE solid;
    height: 1.6rem;
    margin-left: 10px;
    margin-top: 25px;
}
.car .goodsEdit .count-num {
	width: 5.44rem;
	height: 1.2rem;
	text-align: center;
	font-size: .6rem;
	color: #232323;
	border: none;
	margin: 0;
}
.car .count .count-min,
.count-add {

	font-size: 1rem;
	color: #232323;
	text-align: center;
	width: 30px;
	display: inline-block;
}
.car .shopTitle {
	padding: 5px 15px;
}
.car h2 {
	text-align: left;
	display: inline-block;
	font-size: 15px;
	float: left;
	font-weight: normal;
}
.car .shopTitle b {
	display: inline-block;
	width: 18px;
	height: 18px;
	background: url(../imgs/shop.png);
	background-repeat: no-repeat;
	background-size: 100%;
	float: left;
	margin-right: 10px;
}



