
.store-page {
		background-color: #F8F8F8;
		margin-bottom: 2rem;
}
.store-page .banner-index {
	position: relative;
	background-color: #FFFFFF;
}
.store-page .search {
		position: fixed;
		left: 0;
		top: 0;
		width: 100%;
		height: 2rem;
		z-index: 888;
		background-color:rgba(202, 202, 202,.6);
}
.store-page .search.act{
		background-color: #fff;
}
.store-page .search.act .inp input{
		background-color: #eee;
}
.store-page .search.act .cate .cate-pic{
	    background-position: -14.2rem 0;
}
.store-page .search.act .cate span{
		color: #aaa;
}
.store-page .search .inp {
		position: relative;
		width: 80%;
		height: 2rem;
		line-height: 2rem;
		float: left;
		padding-left: 5%;
}
.store-page .search .inp input {
		width: 100%;
		height: 1.4rem;
		border-radius: 5px;
		border: none;
		padding-left: 1.4rem;
}
.store-page .search .inp .search-pic {
		position: absolute;
		left: 6%;
		top: .3rem;
		background-position: -4.6rem 0;
}
.store-page .search .cate {
		width: 20%;
		height: 2rem;
		line-height: 2rem;
		float: left;
		color: #fff;
}
.store-page .search .cate .cate-pic {
		background-position: -12.6rem 0;
		margin-top: 0.3rem;
		height: 1.4rem;
}
.store-page .search .cate span {
		float: right;
		margin-right: .4rem;
		font-size: .7rem;
}
.store-page i.pic {
		display: inline-block;
		width: 1.4rem;
		height: 1.2rem;
		background-image: url(../../assets/store.png);
		background-repeat: no-repeat;
		background-size: cover;
}
.store-page .header {
		width: 100%;
		height: 6rem;
		position: relative;
		color: #fff;
}
.store-page .header .bgi {
		width: 100%;
		height: 100%;
}
.store-page .header .store-mess {
		position: absolute;
		left: 0.4rem;
		bottom: .3rem;
		font-size: .6rem;
		line-height: .8rem;
		text-align: left;
		width: 250px;
}
.store-page .header .store-mess .logo {
		width: 4rem;
		float: left;
		border: 2px solid rgba(255, 255, 255, .6);
		margin-right: .2rem;
}
.store-page .header .store-mess .tag {
		background-color: #DD2727;
		font-size: .5rem;
		border-radius: 2px;
		padding: 1px 2px;
		position: absolute;
    	width: 65px;
    	bottom: 0px;
}
.store-page .header .collect {
		position: absolute;
		right: 0;
		bottom: 0;
		background-color: #DD2727;
		height: 2rem;
		line-height: 2rem;
}
.store-page .header .collect .fans-num {
		background-color: #D0021B;
		display: inline-block;
		height: 100%;
		width: 50%;
		line-height: .6rem;
		float: right;
		font-size: .6rem;
		padding-top: .4rem;
}
.store-page .nav {
		width: 100%;
		height: 3rem;
		background-color: #fff;
}
.store-page .nav span {
		display: inline-block;
		width: 25%;
		height: 3rem;
		float: left;
		padding: .4rem 0;
		border-bottom: 1px solid #EFEFEF;
}
.store-page .nav span.act {
		border-bottom: 2px solid #f00;
}
.store-page .nav span em {
		display: block;
		line-height: 0.2rem;
		font-size: .6rem;
		color: #8A8A8A;
}
.store-page .nav span.act em {
		color: #f00;
}
.store-page .nav span i.home {
		background-position-x: -5.8rem;
}
.store-page .nav span.act i.home {
		background-position-x: .24rem;
}
.store-page .nav span i.goods {
		background-position-x: -8.2rem;
}
.store-page .nav span.act i.goods {
		background-position-x: -2.1rem;
}
.store-page .nav span i.new {
		background-position-x: -6.94rem;
}
.store-page .nav span.act i.new {
		background-position-x: -0.94rem;
}
.store-page .nav span i.active {
		background-position-x: -9.5rem;
}
.store-page .nav span.act i.active {
		background-position-x: -3.4rem;
}
.store-page .banner {
		background-color: #fff;
		margin-top: .6rem;
		border-top: 1px solid #EFEFEF;
		padding: 4% 0;
		position: relative;
}
.store-page .banner img {
		height: 100%;
		width: 100%;
}
.store-page .adv {
		margin-top: 1rem;
}
.store-page .adv .title {
		width: 100%;
		height: 3rem;
		line-height: 3rem;
		color: #fff;
		background-image: url(../../imgs/titlebgi.png);
		background-size: cover;
		background-repeat: no-repeat;
}
.store-page .adv .adv-block {
		background-color: #fff;
		border-bottom: 1px solid #eee;
		overflow: hidden;
		padding: 1rem 0;
}
.store-page .adv .adv-block {
		position: relative;
}
.store-page .adv .adv-block img {
		width: 100%;
		float: left;
}
.store-page .adv .adv-block .adv-info {
		position: absolute;
		right: 20%;
		top: 50%;
		width: 60%;
		background-color: rgba(0, 0, 0, .7);
		padding: 2%;
		font-size: .4rem;
		color: #fff;
}
.store-page .adv .adv-block .name {
		display: inline-block;
		border: 1px solid #FFC90E;
		border-bottom: none;
		width: 100%;
}
.store-page .adv .adv-block .name em {
		display: block;
		font-weight: 600;
		font-size: 1rem;
}
.store-page .adv .adv-block .price {
		display: inline-block;
		border: 1px solid #FFC90E;
		border-top: none;
		width: 100%;
}
.store-page .adv .adv-block .price em {
		display: block;
		color: #FDEE03;
		font-size: .8rem;
}
.store-page .adv .adv-block .price i {
	    display: inline-block;
		font-style: normal;
		width: 4rem;
		height: 1rem;
		line-height: 1rem;
		border: 1px solid #fff;
		margin-bottom: .4rem;
}
.store-page .goods-list {
		margin-top: 1rem;
		overflow: hidden;
}
.store-page .goods-list .list-blick .list-title {
		background-color: #fff;
		line-height: 2rem;
		height: 2rem;
		text-align: left;
		text-indent: 1em;
		font-weight: 100;
		border-top: 1px solid #eee;
}
.store-page .goods-list .list-blick .list-info {
		width: 50%;
		float: left;
		padding: 2%;
		text-align: left;
		background-color: #fff;
}
.store-page .goods-list .list-blick .list-info img {
		width: 100%;
}
.store-page .goods-list .list-blick .list-info p {
		font-size: .6rem;
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
}
.store-page .goods-list .list-blick .list-info span {
		color: #f00;
		font-size: .5rem;
		line-height: 1rem;
}




