
.merchant .storeItem {
	height: 100px;
	padding: 10px 0;
}
.merchant .vux-header .vux-header-left a,
.vux-header .vux-header-left button,
.vux-header .vux-header-right a,
.vux-header .vux-header-right button {

	color: white;
}
.merchant .vux-header .vux-header-left .left-arrow:before {

	border: 1px solid white;
	content: "";
	position: absolute;
	width: 12px;
	height: 12px;
	border-width: 1px 0 0 1px;
	-webkit-transform: rotate(315deg);
	transform: rotate(315deg);
	top: 8px;
	left: 7px;
}
.merchant .storeList {
	padding-bottom: 5px;
}
.merchant .storeItem .storeInfo {
	padding: 10px;
	text-align: left;
	height: 90px;
	border-bottom: 1px #EBEBEB solid;
}
.merchant .storeInfo .storeTitle {
	font-weight: 500;
	font-size: 20px;
	width: 180px;
	overflow: hidden;
	display: -webkit-box;
	-webkit-line-clamp: 1;
	-webkit-box-orient: vertical
}
.merchant .storeInfo h4 {
	font-weight: normal;
	font-size: 13px;
	margin-top: 10px;
	width: 75%;
	color: #6C6C6C;
}
.merchant .storeInfo .range {
	float: right;
	color: #666666;
	margin-left: 230px;
	margin-top: -20px;
	font-size: 13px;
}



