
.feedback .page {
	background-color: white;
	position: relative;
	z-index: 0;
	margin-top: 46px;
}
.feedback .Jform2 {
	padding: 2rem .52rem 0;
}
.feedback .Jform2 textarea {
	height: 5rem;
	margin-bottom: 2rem;
	font-size: .6rem;
	-webkit-appearance: none;
	box-shadow: none;
	border-radius: 0!important;
	background: #eee;
	padding: .56rem .1rem .1rem .6rem;
	width: 100%;
	color: #232323;
	border: 0;
	resize: none;
}
.feedback .button.block,
.feedback button.block {
	width: 100%;
	padding: 0 .3rem;
	line-height: 1.76rem;
	font-size: 0.64rem;
}
.feedback .vux-header .vux-header-left a {
	color: white;
}
.feedback .vux-header .vux-header-left .left-arrow:before {
    content: "";
    position: absolute;
    width: 12px;
    height: 12px;
    border: 1px solid white;
    border-width: 1px 0 0 1px;
    -webkit-transform: rotate(315deg);
    transform: rotate(315deg);
    top: 8px;
    left: 7px;
}
.feedback .button,
.feedback button {
	color: #fff;
	background: #eb6100;
	font-size: .6rem;
	display: inline-block;
	padding: .14rem .3rem;
	border: none;
	cursor: pointer;
}



