
.new-goodsList{
	margin-top: 48px;
}
.new-goodsList .list{
	overflow: hidden;
	margin-bottom: 20px;
}
.new-goodsList .list div.date-item{
	height: 2rem;
	line-height: 2rem;
}
.new-goodsList .list div.date-item span{
	background-color: #EEEEEE;
	padding: .6rem;
	font-size: .6rem;
	color: #666;
}
.new-goodsList .list div.date-item:after{
	content: '';
	display: block;
	border-bottom: 1px solid #dcdcdc;
	margin-top: -1rem;
}
.new-goodsList .list .item {
	width: 50%;
	float: left;
	border-right: 1px solid #eee;
	border-bottom: 1px solid #eee;
	padding: 2%;
	text-align: left;
	background-color: #fff;
}
.new-goodsList .list .item img {
	width: 171.5px;
	height: 171.5px;
}
.new-goodsList .list .item p {
	font-size: .6rem;
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
	height: 42px;
}
.new-goodsList .list .item span {
	font-size: .5rem;
	color: #fff;
	background-color: #f00;
	padding: 1px;
	margin-right: .2rem;
}
.new-goodsList .list .item span.price {
	color: #f00;
	background-color: #fff;
}



